.main-card-container {
  width: 280px;
  height: 100%;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 768px) {
    width: 610px;
  }
  @media screen and (min-width: 1280px) {
    width: 946px;
  }
}

