.modal {
  position: relative;
  background: #fff2fd;
  border-radius: 150px 0px;
  height: 915px;
  width: 272px;
  margin-left: auto;
  margin-right: auto;
  @media screen and (min-width: 768px) {
    height: 831px;
    width: 595px;
    border-radius: 300px 0px;
  }
  @media screen and (min-width: 1280px) {
    height: 1023px;
    width: 942px;
    border-radius: 300px 0px;
  }
}
.flex {
  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    flex-basis: calc(100% / 2 - 30px);
  }
}

.img-modal {
  padding-top: 141px;
  padding-left: 50px;
  padding-right: 30px;
  width: 233px;
  @media screen and (max-width: 767px) {
    display: none;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 235px;
    padding-right: 60px;
    width: 360px;
  }
}
//
.modal__title-list {
  padding-bottom: 10px;
  @media screen and (min-width: 1280px) {
    padding-bottom: 20px;
  }
}

.modal__title {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 1.2;
  text-align: center;
  margin-bottom: 5px;
  color: #dc56c5;
  @media screen and (min-width: 768px) {
    text-align: left;
    margin-bottom: 7px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 24px;
  }
}
.modal__title-description {
  font-family: Montserrat;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  display: block;
  color: #0e0e0e;
  @media screen and (min-width: 768px) {
    text-align: left;
  }
  @media screen and (min-width: 1280px) {
    font-size: 18px;
  }
}
.size-description-title {
  width: 243px;
  @media screen and (min-width: 1280px) {
    width: 450px;
  }
}
.description-size {
  width: 178px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  @media screen and (min-width: 768px) {
    margin-left: auto;
    margin-left: 0px;
  }
}

.close-modal {
  position: absolute;
  cursor: pointer;
  top: 25px;
  right: 25px;
  @media screen and (min-width: 1280px) {
    top: 30px;
    right: 30px;
  }
  // opacity: 0;
  // backface-visibility: hidden;
  // transition: opacity $speed $easing, transform $speed $easing;
  // transition-delay: $delay;

  svg {
    width: 17px;
    height: 17px;
    @media screen and (min-width: 1280px) {
      width: 25px;
      height: 25px;
    }
  }
}

.box-image {
  width: 102px;
  height: 102px;
  background-image: url(https://cdn.fishki.net/upload/post/2018/11/19/2772341/bez-nazvanija.jpg);
  background-size: 132px 132px;
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  border-radius: 100%;
  margin-left: auto;
  margin-right: auto;
  left: 50%;
  transform: translate(-50%, 75px);
  @media screen and (min-width: 768px) {
    width: 70px;
    height: 70px;
    transform: translate(-50%, 25px);
  }
  @media screen and (min-width: 1280px) {
    width: 132px;
    height: 132px;
    transform: translate(-50%, 62px);
  }
}
.modal-container {
  padding-top: 195px;
  @media screen and (min-width: 768px) {
    padding-top: 141px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 235px;
  }
}
